html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: 900;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h1, h2, h3 {
  margin: 0;
}

.no-margin {
  margin: 0;
}

.bg-green {
  background-color: #4CB8B3;
}

.bg-yellow {
  background-color: #FEF46D;
}

.header {
  background-color: #4CB8B3;
}
.header .header-content {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .header .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: #FFF;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}

.nav-main {
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.nav-main a {
  color: #FFF;
  font-size: 2rem;
}
.nav-main a:hover {
  color: #FEF46D;
}

.btn-home {
  position: fixed;
  width: 4rem;
  height: 4rem;
  background-color: #4CB8B3;
  color: #FFF;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 3rem;
  border-radius: 50%;
  bottom: 0;
  right: 4rem;
}
.btn-home:hover {
  cursor: pointer;
  color: #FEF46D;
}

.video {
  position: relative;
  max-height: 60rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+62,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
}

.video-content {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.video-content h2 {
  display: flex;
  flex-direction: column;
  color: #FFF;
  font-size: 4rem;
  align-items: flex-start;
}
@media (min-width: 478px) {
  .video-content h2 {
    font-size: 7rem;
  }
}
.video-content h2 span {
  background-color: #F53756;
  font-size: 1.3rem;
  padding: 1rem 2rem;
}
@media (min-width: 478px) {
  .video-content h2 span {
    font-size: 2rem;
  }
}

.info {
  margin-top: 4rem;
}
@media (min-width: 928px) {
  .info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.info-text {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .info-text {
    margin-top: 0;
  }
}
.info-text .date {
  color: #F53756;
  font-weight: 700;
}
.info-text .text {
  font-size: 2rem;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
  margin-top: 4rem;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFF;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenary-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenary-container .escenary {
  padding-top: 2rem;
}
.escenary-container .escenary-name {
  color: #000;
  font-size: 2rem;
  font-weight: 300;
}
.escenary-container .escenary-name:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.rock {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .rock {
    margin-bottom: 0;
  }
}

.edm .escenary-name:after {
  background-image: url(../../../build/img/icono_edm.png);
}

.rock .escenary-name:after {
  background-image: url(../../../build/img/icono_rock.png);
}

.schedule {
  padding: 0;
  list-style: none;
  margin-bottom: 0;
}
.schedule li {
  padding: 2.5rem;
  font-weight: 700;
}
.schedule li span {
  font-weight: 400;
}

.bg-yellow .schedule li:nth-child(odd) {
  background-color: #b7ab01;
}

.bg-green .schedule li:nth-child(even) {
  background-color: #3b9691;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-img {
  padding: 0;
  list-style: none;
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .gallery-img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .gallery-img {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}
.overlay img {
  width: 90%;
}
@media (min-width: 1200px) {
  .overlay img {
    width: initial;
  }
}

.btn-close {
  width: 3rem;
  height: 3rem;
  background-color: #FFF;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 2rem;
  border-radius: 50%;
}
.btn-close:hover {
  cursor: pointer;
}

.fixed {
  overflow: hidden;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #FFF;
}
.tickets .tickets-container {
  margin-top: 2rem;
}
@media (min-width: 478px) {
  .tickets .tickets-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.tickets .plan p {
  color: #FFF;
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pass {
  width: 100%;
  border-spacing: 0;
}
.pass thead {
  background-color: #F53756;
}
.pass thead th {
  padding: 1.2rem;
  color: #FFF;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 1.8rem;
}
.pass tbody {
  background-color: #FFF;
}
.pass tbody td {
  padding: 1.2rem;
  border: 0.5px solid #e1e1e1;
}
.pass tfoot {
  background-color: #752F97;
}
.pass tfoot td {
  padding: 1.2rem;
  color: #FFF;
  text-align: center;
  font-size: 2.3rem;
}

.footer {
  background-color: #F53756;
  color: #FFF;
  padding: 1.8rem;
  text-align: center;
}